<template>
 
<div class="new">
  <ul class="line" ref="con1" :class="{anim:animate==true}">
    <li v-for="item in items" :key="item.id"  @mouseenter="stopscoll" @mouseleave="startscoll" ><span>{{item.name}}</span><span class="auther"></span><span class="time">2000-10-10</span></li>
  </ul>
</div>
</template>
 
<script>
export default {
  data() {
  return {
      timer:null,
      animate:false,
      items:[
          {name:""},
          {name:""},
          {name:""},
          {name:""},
          {name:"00"},
          {name:""},
          {name:""},
         
      ]
  }
},
created(){
     clearInterval(this.timer)
            this.timer = null
            this.setTimer()
},
 destroyed: function () {
            // 
            clearInterval(this.timer)
            this.timer = null
        },

methods: {
    stopscoll(){
        clearInterval(this.timer)
        this.timer=null
    },
    startscoll(){
        clearInterval(this.timer)
            this.timer = null
            this.setTimer()
    },
    setTimer(){
        var that = this;
        if(that.timer==null){
            that.timer=setInterval(this.scroll,2000)
        }
    }, 
     
    scroll(){
       this.animate=true;    // css3true
       setTimeout(()=>{      //  es6this
               this.items.push(this.items[0]);  // 
               this.items.shift();               //
               this.animate=false;  // margin-top 0 
       },500)
    }

}
}
</script>
 
<style scoped>
*{
    margin: 0 ;
    padding: 0;
}
.new{
    width: 100%;
    height:400px;
    overflow: hidden;
    margin: 0;
    
    
}
.anim{
    transition: all 0.5s;
    margin-top: -30px;
}
.new li{
    list-style: none;
    line-height: 30px;
    height: 30px;
    width: 100%;
    text-align: left;
    cursor: pointer;
    color: #fff;
}
.new ul{
    color:blanchedalmond;width:100%;height:150px;overflow: hidden;margin-left: 10%;position: relative;list-style: none;
}

.new ul.line li span.auther{
    position: absolute;left:50%;
}
.new ul.line li span.time{
    position: absolute;left:70%;
}
</style>



